import React, { useEffect, useState, useRef } from 'react';
import { Title, Center, Paper, Stack, ScrollArea, Badge, Button } from '@mantine/core';
import Plot from "react-plotly.js";
import scrollBarClass from './ScrollBar.module.css';
import { useSelector } from 'react-redux';
import MakeGraph from './MakeGraph.tsx';



export default function ScienceBox({ width, height, position, variable, landscape }) {
    // TODO: Do I need to accound for the padding here?
    const graphHeight = height * 0.5;
    const graphWidth = width * 0.9;
    const scrollHeight = landscape ? (height * 0.46) : (height * 0.4);
    const titleHeight = height * 0.01;
    const gap = height * 0 // Because title sorts the gap

    // const myStyle = {
    //     '--title-fw': '100 !important',
    //     '--title-lh': '100 !important',
    //     'lineHeight': '100 !important',
    // }

    const variableSplit = variable.split(" ")
    const titleText = variableSplit[0];
    let aggregation = "monthly"
    let statistic = "climatology"
    let period = "1980-2010"
    let period2: string | null = null
    let color1 = 'maroon'
    let color2: string | null = null
    switch (variable) {
        case "Temperature daily climatology":
            aggregation = 'daily'
            // period2 = "2000-2020"
            color1 = 'maroon'
            // color2 = 'red'
            break
        case "Rainfall annual climatology":
            color1 = 'darkblue'
            color2 = 'maroon'
            period2 = "2000-2020"
            break
        case "Temperature annual climatology":
            period2 = "2000-2020"
            color1 = 'maroon'
            color2 = 'red'
            break
        case "Rainfall annual timeseries":
            color1 = 'darkblue'
            aggregation = "annual"
            statistic = "timeseries"
            period = "1940-2024"
            break
        case "Temperature annual timeseries":
            aggregation = "annual"
            statistic = "timeseries"
            period = "1940-2024"
            break
        case "Temperature warming stripes":
            aggregation = "annual"
            statistic = "warming stripes"
            period = "1940-2024"
            break
        case "Wind-speed annual climatology":
            color1 = 'darkgreen'
            period2 = "2000-2020"
            color2 = 'turquoise'
            break
        default:
            break
    }

    if (color2 === null) {
        color2 = color1
    }

    const angle = -10
    const borderColor = 'lightgray'

    const jointButtonStyle = {
        borderRadius: 100,
        borderColor: borderColor,
        borderWidth: 2,
        // boxShadow: "2px 2px #00000022",
        backgroundImage: `linear-gradient(${angle}deg, ${color2} 35%, ${color1} 65%)`,
    }

    const singleButtonStyle1 = {
        borderRadius: 100,
        borderColor: borderColor,
        borderWidth: 2,
        backgroundColor: color1,
    }

    const singleButtonStyle2 = {
        borderRadius: 100,
        borderColor: borderColor,
        borderWidth: 2,
        backgroundColor: color2,
    }

    const shadowOffset = 3
    const heightWithoutShadow = landscape ? height : (height - shadowOffset)

    const viewport = useRef<HTMLDivElement>(null);
    const scrollToTop = () => viewport.current!.scrollTo({ top: 0, behavior: 'smooth' });

    const displayGraphs = useSelector(state => state.graph.graphsVisible)
    useEffect(() => {
        console.log("scrolling science boxes back to top")
        scrollToTop()
    }, [displayGraphs])

    return (
        <Paper
            // shadow='xl'
            withBorder
            className='cardClass'
            styles={{
                root: {
                    // padding: '20px',
                    borderRadius: '10px',
                    width: `${width}px`,
                    height: `${heightWithoutShadow}px`,
                    display: 'flex',
                    justifyContent: 'center',
                    alignItems: 'center',
                    borderColor: "#94133377",
                    borderWidth: "2px",
                    boxShadow: `${shadowOffset}px ${shadowOffset}px #00000022`,
                },
            }}
        >

            <Stack
                h={height}
                align='center'
                justify='flex-start'
                gap={gap}
                styles={{
                    root: {
                        padding: '20px',
                    }
                }}
            >
                <MakeGraph width={graphWidth} height={graphHeight} position={position} variable={variable} colors={[color1, color2]} />

                <ScrollArea
                    h={scrollHeight}
                    type="auto"
                    scrollbars="y"
                    classNames={scrollBarClass}
                    viewportRef={viewport}
                    styles={{
                        root: {
                            justifyItems: 'left',
                            textAlign: "left"
                        }
                    }}
                >
                    <p className='CardTitle'>
                        {titleText}
                    </p>
                    <p className='CardPara'>
                        <Badge
                            // variant="gradient"
                            // gradient={{ from: color1, to: color2, deg: 90 }}
                            style={jointButtonStyle}
                        >
                            {aggregation}
                        </Badge>
                        <span> </span>
                        <Badge
                            // variant="gradient"
                            // gradient={{ from: color1, to: color2, deg: 90 }}
                            style={jointButtonStyle}
                        >
                            {statistic}
                        </Badge>
                        <span> </span>
                        <Badge
                            // variant="gradient"
                            // gradient={{ from: color1, to: color1, deg: 90 }}
                            style={singleButtonStyle1}
                        >
                            {period}
                        </Badge>
                        {(period2 !== null) && (
                            <>
                                <span> </span>
                                <Badge
                                onClick={() => console.log("Clicked")}
                                    // variant="gradient"
                                    // gradient={{ from: color2, to: color2, deg: 90 }}
                                    style={singleButtonStyle2}
                                >
                                    {period2}
                                </Badge>
                            </>
                        )}
                    </p>
                    <p className='CardPara'>
                        "Sed ut perspiciatis unde omnis iste natus error sit voluptatem accusantium doloremque laudantium, totam rem aperiam, eaque ipsa quae ab illo inventore veritatis et quasi architecto beatae vitae dicta sunt explicabo. Nemo enim ipsam voluptatem quia voluptas sit aspernatur aut odit aut fugit, sed quia consequuntur magni dolores eos qui ratione voluptatem sequi nesciunt. Neque porro quisquam est, qui dolorem ipsum quia dolor sit amet, consectetur, adipisci velit, sed quia non numquam eius modi tempora incidunt ut labore et dolore magnam aliquam quaerat voluptatem. Ut enim ad minima veniam, quis nostrum exercitationem ullam corporis suscipit laboriosam, nisi ut aliquid ex ea commodi consequatur? Quis autem vel eum iure reprehenderit qui in ea voluptate velit esse quam nihil molestiae consequatur, vel illum qui dolorem eum fugiat quo voluptas nulla pariatur?"
                    </p>
                </ScrollArea>
            </Stack>
        </Paper>
    )
};

