import React, { useState, useRef, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { updatePosition, updateName } from '../reducers/locationSlice.js'
import { labelFromAdminLevels } from './Location.ts'

// Function to get the user's location
const fetchGeolocation = () => {
  return new Promise((resolve, reject) => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          resolve({
            latitude: position.coords.latitude,
            longitude: position.coords.longitude
          });
        },
        (error) => reject(error)
      );
    } else {
      reject(new Error("GEO: Geolocation is not supported by this browser."));
    }
  });
};

const findNearestPlace = async (latitude, longitude) => {
  const longitudeWrapped = normalizeLongitude(longitude)

  const url = `https://nominatim.openstreetmap.org/reverse?format=geocodejson&lat=${latitude}&lon=${longitudeWrapped}&addressdetails=1`;
  console.log(`Used URL: ${url}`);
  try {
    const response = await fetch(url);
    const data = await response.json();

    const adminLevels = data.features[0].properties.geocoding.admin;
    const city = labelFromAdminLevels(adminLevels)

    console.log(`Got city: ${city}`);
    return city
  } catch (error) {
    // dispatch(updateName(''))
    console.log("Error fetching data from Nominatim.");
    return null
  }
};

function normalizeLongitude(longitude) {
  // Normalize the longitude to be within the range of -180 to 180
  longitude = ((longitude + 180) % 360 + 360) % 360 - 180;
  return longitude;
}

const Geolocation = () => {
  const [position, setPosition] = useState(null);
  const [placeName, setPlaceName] = useState(null);
  const haveGeolocated = useSelector(state => state.location.haveGeolocated)
  const dispatch = useDispatch()

  useEffect(() => {
    // Function to handle the async call to get geolocation
    const getLocation = async () => {
      try {
        const result = await fetchGeolocation();
        setPosition(result);
        const place = await findNearestPlace(result.latitude, result.longitude);
        setPlaceName(place);
      } catch (error) {
        console.error("GEO: Error fetching geolocation:", error);
      }
    };

    getLocation();
  }, []); // Empty dependency array ensures this runs once on mount

  useEffect(() => {
    if ((position !== null) && (placeName !== null)) {
      // This effect runs when location changes (i.e., async geolocation call finishes)
      // Here you can perform actions based on the latest state value
      if (!haveGeolocated) {
      dispatch(updatePosition({ lat: position.latitude, lng: position.longitude }));
      dispatch(updateName(placeName))
      }
    }
  }, [position, haveGeolocated, dispatch, placeName]); // Add myStateVariable to dependencies

  return (
    <></>
  );
};

export default Geolocation;