import React, { useEffect, useRef, useState } from 'react';
import { Combobox, Loader, TextInput, useCombobox } from '@mantine/core';

import { useSelector, useDispatch } from 'react-redux'
import { updatePosition, updateName, setGeolocated } from '../reducers/locationSlice.js'
import { useFocusReturn } from '@mantine/hooks';

import { labelFromAdminLevels } from './Location.ts'
// import '@mantine/core/styles/Combobox.css';
// import myComboboxStyle from './Combobox.module.css';

// Function to make an external API call and return a JSON object
async function fetchJsonData(apiUrl: string, signal: AbortSignal) {
  try {
    // Make the API call using fetch with an abort signal
    const response = await fetch(apiUrl, { signal });

    // Check if the response is ok (status is in the range 200-299)
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }

    // Convert the response to JSON
    const data = await response.json();

    let namesAndLocations = data.features.filter(feature => {
      const admin = feature.properties.geocoding.admin;
      return admin && (admin.level6 || admin.level7 || admin.level8);
    }).reduce((acc, feature) => {
      // const label = feature.properties.geocoding.label;
      const label = labelFromAdminLevels(feature.properties.geocoding.admin);
      const position = [feature.geometry.coordinates];

      // Check if label is already in the Set
      if (!acc.seenLabels.has(label) && (label !== 'Unknown')) {
        acc.seenLabels.add(label); // Add label to Set
        acc.result.push([label, position]); // Push to result array
      }
      return acc;
    }, { result: [], seenLabels: new Set() }).result;

    // namesAndLocations = [...new Set(namesAndLocations)];  // Remove duplicates

    const maxOptionsToDisplay = 4;
    namesAndLocations = namesAndLocations.slice(0, maxOptionsToDisplay)  // Now truncate to some number

    // Return the JSON data
    return namesAndLocations;
  } catch (error) {
    // Handle abort errors differently from other errors
    if (error.name === 'AbortError') {
      console.log('Fetch aborted');
    } else {
      console.error('Error fetching data:', error);
    }
    throw error;
  }
}



export default function LocationSearchBar({barWidth}) {
  // const returnFocus = useFocusReturn({
  //   opened: false,
  //   shouldReturnFocus: false,
  // });

  const combobox = useCombobox({
    onDropdownClose: () => {
      combobox.resetSelectedOption()
      // returnFocus()
    },
  });

  const locationName = useSelector(state => state.location.locationName)

  const [loading, setLoading] = useState(false);
  const [data, setData] = useState<Array<{ string: number[] }> | null>(null);

  const [value, setValue] = useState<string>(locationName);

  const [empty, setEmpty] = useState(false);
  const abortController = useRef<AbortController>();

  // We need to useEffect to set the value if we want it set here, otherwise the value is actually only set ON THE FIRST RENDER!
  useEffect(() => {
    setValue(locationName)
    setData(null);
  }, [locationName])

  const dispatch = useDispatch()

  const fetchOptions = (query: string) => {
    abortController.current?.abort();
    abortController.current = new AbortController();
    setLoading(true);

    const queryURL = `https://nominatim.openstreetmap.org/search?q=${query}&format=geocodejson&addressdetails=1`
    console.log("queryURL", queryURL);

    fetchJsonData(queryURL, abortController.current.signal)
      .then((result) => {
        setData(result);
        setLoading(false);
        setEmpty(result.length === 0);
        abortController.current = undefined;
      })
      .catch(() => { });
  };

  const options = (data || [[]]).map((item, index) => (
    <Combobox.Option value={item[0]} key={index}>
      {item[0]}
    </Combobox.Option>
  ));

  const findNumbersArray = (searchString: string): number[] | null => {
    let newPosition: number[] | null = null

    if (data) {
      data.forEach((item, index) => {
        if (newPosition !== null) return;
        if (item[0] === searchString) {
          newPosition = item[1]
          return newPosition // This return only returns from the forEach loop and not the wider {} block
        }
      });
    };

    console.log('IS THIS POSSIBLE? findNumbersArray failed:', searchString, data);
    return newPosition;
  };

  const emptyBoxText = loading ? "Searching..." : "No results found."

  return (
    <Combobox
      onOptionSubmit={(optionValue) => {
        setValue(optionValue);
        combobox.closeDropdown();
        const newPosition = findNumbersArray(optionValue);
        console.log('Option submitted:', optionValue, value, newPosition, data);
        if (newPosition !== null) {
          const newPositionStruct = { lat: newPosition[0][1], lng: newPosition[0][0] }
          dispatch(updatePosition(newPositionStruct))
          dispatch(updateName(optionValue))
        }
      }}
      withinPortal={false}
      store={combobox}
      size='md'
      // width={200}  // Required to avoid crashes due to resizing happening too often
      width={barWidth}
      styles={{
        options: {
          pointerEvents: 'auto',
          // width: barWidth,
        },
        dropdown: {
          borderRadius: '10px',
          // margin: '0 13px',
          borderColor: "#94133377",
          borderWidth: "2px",
        }
      }}
    >
      <Combobox.Target>
        <TextInput
        w={barWidth}
          placeholder=" Search location"
          // placeholder={placeholder}
          value={value}
          // value={locationName}
          onChange={(event) => {
            setValue(event.currentTarget.value);
            fetchOptions(event.currentTarget.value);
            combobox.resetSelectedOption();
            combobox.openDropdown();
            console.log("opening dropdown")
          }}
          onClick={() => combobox.openDropdown()}
          onFocus={() => {
            // handleMapClick('asd')
            combobox.openDropdown();
            if (data === null) {
              fetchOptions(value);
            }
          }}
          onBlur={() => {
            combobox.closeDropdown()
            console.log("closing dropdown on BLUR")
          }
          }
          rightSection={loading && <Loader size={18} />}
          styles={{
            root: {
              padding: '0',
              pointerEvents: 'auto',
              // minWidth: `${barWidth}px`,
              // maxWidth: `${barWidth}px`,
            },
            input: {
              borderRadius: '100px',
              pointerEvents: 'auto',
              color: 'black',
              borderColor: "#94133377",
              borderWidth: "2px",
            },
          }}
        />
      </Combobox.Target>

      <Combobox.Dropdown hidden={data === null}>
        <Combobox.Options>
          {options}
          {empty && <Combobox.Empty>{emptyBoxText}</Combobox.Empty>}
        </Combobox.Options>
      </Combobox.Dropdown>
    </Combobox>
  );
}