import { createSlice } from '@reduxjs/toolkit'

export const locationSlice = createSlice({
    name: 'location',
    initialState: {
        locationName: "Reading, Melbourne, Australia",
        locationPosition: { lat: -37.86836, lng: 144.9797 },
        haveGeolocated: false,
        doSpin: false,
    },
    reducers: {
        update: (state, newLocationName, newLocationPosition) => {
            state.locationName = newLocationName.payload;
            state.locationPosition = newLocationPosition.payload;
        },
        updatePosition: (state, newLocationPosition) => {
            state.locationPosition = newLocationPosition.payload;
        },
        updateName: (state, newLocationName) => {
            state.locationName = newLocationName.payload;
            state.doSpin = true;
            // state.doSpin = false;
        },
        setGeolocated: state => {
            state.haveGeolocated = true
        },
        resetSpin: (state) => {
            state.doSpin = false
        }
    }
})

// Action creators are generated for each case reducer function
export const { update, updatePosition, updateName, setGeolocated, resetSpin } = locationSlice.actions

export default locationSlice.reducer