import React from 'react';
import { ActionIcon } from '@mantine/core';
import CalendarIcon from './icons/CalendarIcon';

import { useSelector, useDispatch } from 'react-redux'
import { toggleCalendar, hideCalendar } from '../reducers/calendarSlice'
import { useClickOutside } from '@mantine/hooks';

function ToggleCalendar({buttonWidth}) {
  const displayCalendar = useSelector(state => state.calendar.calendarVisible)
  const dispatch = useDispatch()

  const buttonStyle = {
    width: `${buttonWidth}px`,
    height: `${buttonWidth}px`,
    padding: "2%",
    margin: "0px",
    '--ai-bg': 'rgb(0, 0, 0, 0.2)',
    '--ai-hover': 'rgb(255, 255, 255, 0.3)',
    pointerEvents: "auto",
    borderColor: "#94133377",
    borderWidth: "2px",
  }

  const handleClickOutside = () => {
    dispatch(hideCalendar());
  };

  const ref = useClickOutside(handleClickOutside, ['mousedown']);

  return (
    <>
      <ActionIcon
        variant="filled"
        style={buttonStyle}
        className={displayCalendar ? 'buttonActive' : ''}
        onClick={() => dispatch(toggleCalendar())}
        styles={{
          root: {
            padding: '0',
            borderRadius: '100px',
          }
        }}
        ref={ref}
      >
        <CalendarIcon />
      </ActionIcon>
    </>
  );
}

export default ToggleCalendar;