import React, { useEffect, useState } from 'react';
import { Title, Center, Paper, Stack, ScrollArea, Badge, Box } from '@mantine/core';
import Plot from "react-plotly.js";
import scrollBarClass from './ScrollBar.module.css';
import { useSelector } from 'react-redux';
import { Grid } from 'semantic-ui-react';

export default function MakeGraph({ width, height, position, variable, colors }) {
    function getDecimalPart(number) {
        // Just while testing with random data
        return number % 1;
    }

    function generateAngles(N) {
        if (N <= 0) {
            throw new Error("N must be a positive integer");
        }

        let angles = [];
        let step = 360 / N;

        for (let i = 0; i < N; i++) {
            angles.push(i * step);
        }

        return angles;
    }

    function generateScatteredLinearTrend(length, slope, scatter) {
        let data: number[] = [];

        for (let i = 0; i < length; i++) {
            // Calculate the linear trend value
            let trendValue = i * slope;

            // Generate a random scatter value within the range of [-scatter, scatter]
            let randomScatter = (Math.random() * 2 - 1) * scatter;

            // Add the scatter to the trend value
            let scatteredValue = trendValue + randomScatter;

            // Add the result to the data array
            data.push(scatteredValue);
        }

        return data;
    }

    function generateScatteredSineWave(amplitude, offset, scatter, length = 12) {
        let data: number[] = [];

        for (let i = 0; i < length; i++) {
            // Calculate the linear trend value
            let sineValue = Math.sin(i * Math.PI / length) * amplitude

            // Generate a random scatter value within the range of [-scatter, scatter]
            let randomScatter = (Math.random() * 2 - 1) * scatter;

            // Add the scatter to the trend value
            let scatteredValue = sineValue + randomScatter + offset;

            // Add the result to the data array
            data.push(scatteredValue);
        }

        return data;
    }

    // Example usage:
    let length = 100; // Number of data points
    let slope = 2;    // Slope of the linear trend
    let scatter = 5;  // Degree of scatter

    let scatteredData = generateScatteredLinearTrend(length, slope, scatter);

    function shuffleArray(array) {
        for (let i = array.length - 1; i > 0; i--) {
            // Pick a random index from 0 to i
            const j = Math.floor(Math.random() * (i + 1));
            // Swap elements array[i] and array[j]
            [array[i], array[j]] = [array[j], array[i]];
        }
        return array;
    }

    function yvalsToStripes(yvals, centreWhite = true) {
        const yvalsAvg = getAverage(yvals)
        const yvalsAnom = yvals.map(val => val - yvalsAvg)
        const yvalsAbs = absArray(yvalsAnom)
        const absMax = Math.max(...yvalsAbs)
        markerColor = yvalsAnom.map(
            function (value) { return getStripeColor(value, absMax, centreWhite) }
        )
        return markerColor
    }

    function getStripeColor(value, absMax, centreWhite = true) {
        const centralColor = centreWhite ? '#ffffff' : '#dddddd'

        const stripeColors = [
            '#08306b', '#08519c', '#2171b5', '#4292c6',
            '#6baed6', '#9ecae1', '#c6dbef', '#deebf7', centralColor,
            '#fee0d2', '#fcbba1', '#fc9272', '#fb6a4a',
            '#ef3b2c', '#cb181d', '#a50f15', '#67000d',
        ];

        const halfLength = (stripeColors.length - 1) / 2
        const indexRaw = value * halfLength / absMax
        const indedRounded = Math.round(indexRaw)
        const indexShifted = indedRounded + halfLength
        const indexClamped = Math.min(Math.max(0, indexShifted), stripeColors.length)

        return stripeColors[indexClamped]
    }

    const absArray = (array) => {
        return array.map(Math.abs);
    }

    const getAverage = (array) =>
        array.reduce((sum, currentValue) => sum + currentValue, 0) / array.length;


    let yvals: number[] = [0, 1]

    let seasonal_strings = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"]

    let startYear = 1940;
    let endYear = 2024;
    let annual_values = Array.from({ length: endYear - startYear + 1 }, (v, k) => k + startYear);

    let markerColor = colors[0]
    let plotType, xvals, plotMode
    let xAxisRange: number[] | null = null
    let base: number[] | null = null
    let dtick: number | null = null
    switch (variable) {
        case "Temperature daily climatology":
            xvals = generateAngles(365)
            base = generateScatteredSineWave(3, 10, 1, 365)
            yvals = Array(base.length).fill(5)
            break
        case "Rainfall annual climatology":
            // markerColor = "#275eb0";
            plotType = "bar"
            plotMode = "markers"
            xvals = seasonal_strings
            base = generateScatteredSineWave(5, 3, 1)
            yvals = Array(base.length).fill(5)
            break
        case "Temperature annual climatology":
            // markerColor = "#941333";
            plotType = "bar"
            plotMode = "markers"
            xvals = seasonal_strings
            yvals = generateScatteredSineWave(10, 3, 1)
            base = yvals.map(val => val - 1)
            break
        case "Rainfall annual timeseries":
            // markerColor = "#275eb0";
            plotType = "scatter"
            plotMode = "lines+markers"
            xvals = annual_values
            xAxisRange = [1939.5, 2024.5]
            dtick = 10
            yvals = shuffleArray(generateScatteredLinearTrend(xvals.length, 0.1, 0.1))
            base = yvals.map(val => val - 1)
            break
        case "Temperature annual timeseries":
            // markerColor = "#941333";
            plotType = "scatter"
            plotMode = "lines+markers"
            xvals = annual_values
            xAxisRange = [1939.5, 2024.5]
            dtick = 10
            yvals = generateScatteredLinearTrend(xvals.length, 0.03, 0.5)
            base = yvals.map(val => val - 1)
            break
        case "Temperature warming stripes":
            plotType = "bar"
            xvals = annual_values
            xAxisRange = [1939.5, 2024.5]
            dtick = 10
            yvals = generateScatteredLinearTrend(xvals.length, 0.03, 0.5)
            markerColor = yvalsToStripes(yvals)
            yvals = Array(xvals.length).fill(1)
            break
        case "Wind-speed annual climatology":
            // markerColor = "#0b730f";
            plotType = "bar"
            yvals = generateScatteredSineWave(10, 5, 2)
            base = yvals.map(val => val * 0.8)
            xvals = seasonal_strings
            break
        default:
            // markerColor = "#000000";
            plotType = "bar"
            yvals = generateScatteredSineWave(10, 5, 2)
            base = yvals.map(val => val - 1)
            xvals = seasonal_strings
            break
    }


    let data = [
        {
            x: xvals,
            y: yvals,
            base: base,
            mode: plotMode,
            type: plotType,
            name: "A",
            marker: { color: markerColor },
        },
    ]

    if (colors[0] !== colors[1]) {
        const yvals2 = yvals.map(val => val * 1.2)
        const base2 = base?.map(val => val * 1.2)

        data.push({
            x: xvals,
            y: yvals2,
            base: base2,
            mode: plotMode,
            type: plotType,
            name: "A",
            marker: { color: colors[1] },
        })
    }

    let layout = {
        xaxis: {
            showgrid: false,
            fixedrange: true,
            range: xAxisRange,
            tickangle: -45,
            tickmode: "linear", //  If "linear", the placement of the ticks is determined by a starting position `tick0` and a tick step `dtick`
            dtick: dtick,
        },
        yaxis: {
            showgrid: false,
            fixedrange: true,
        },
        width: `${width}`,
        height: `${height}`,
        showlegend: false,
        margin: {
            b: 35,
            l: 0,
            r: 0,
            t: 0,
        },
        paper_bgcolor: 'transparent',
        plot_bgcolor: 'transparent',
        barcornerradius: 5,
    }

    if (variable === "Temperature daily climatology") {
        const yvalsMax = Math.max(...yvals)
        const baseMax = Math.max(...base)
        const yvalsPbase = yvals.map(function (num, idx) {
            return num + base[idx];
        });
        const yvalsMn = yvals.map(function (num, idx) {
            return (num + base[idx]) / 2;
        });
        const dataMin = Math.min(...base)
        const dataMax = Math.max(...yvalsPbase)
        const dataMinPlot = dataMin - 1
        const dataMaxPlot = dataMax

        const cumulDays = [0, 31, 59, 90, 120, 151, 181, 212, 243, 273, 304, 334, 365]

        const scaling = 360 / 365

        data = []
        for (let i = 0; i < (cumulDays.length - 1); i += 2) {
            let r: number[] = [dataMinPlot]
            let theta: number[] = [cumulDays[i] * scaling]
            const jfinal = cumulDays[i + 1] + 1
            for (let j = cumulDays[i]; j < jfinal; j++) {
                r.push(dataMaxPlot)
                theta.push(j * scaling)
            }
            r.push(dataMinPlot)
            theta.push(jfinal)
            const newData = {
                type: "scatterpolar",
                mode: "lines",
                r: r,
                color: 'transparent',
                theta: theta,
                fill: "toself",
                fillcolor: 'rgba(0, 0, 0, 0.1)',
                line: {
                    color: 'transparent',
                },
                name: "",
            }
            data.push(newData)
        }

        const stripesColors = yvalsToStripes(yvalsMn, false)

        const actualData = {
            type: "barpolar",
            mode: "lines",
            r: yvals,
            base: base,
            theta: xvals,
            fill: "toself",
            marker: { color: stripesColors },
            name: "",
        }

        data.unshift(actualData)  // Prepend

        let annotations = []

        const r = 1
        const degToR = (Math.PI / 180)
        const paperAspectRatio = height / width
        const fontSize = height * 0.04
        for (let i = 0; i < (seasonal_strings.length); i++) {

            const theta = (cumulDays[i] + 15) * scaling
            const x = (r * Math.cos(theta * degToR) / 2 * paperAspectRatio) + 0.5
            const y = (r * Math.sin(theta * degToR) / 2 + 0.5)
            const newAnnotation = {
                x: x,
                y: y,
                xref: 'x domain',
                yref: 'y domain',
                showarrow: false,
                text: seasonal_strings[i],
                font: {
                    color: "black",
                    weight: 1000,
                    size: fontSize,
                },
            }
            annotations.push(newAnnotation)
        }

        layout = {
            polar: {
                bgcolor: 'rgba(0, 0, 0, 0)',

                radialaxis: {
                    showgrid: true,
                    fixedrange: true,
                    visible: true,
                    range: [dataMinPlot, dataMaxPlot],
                    layer: "above traces",
                    tickmode: 'array',
                    // tickvals: tickvals,
                    tickfont: {
                        color: "black",
                        weight: 1000,
                        size: fontSize,
                    },

                },
                angularaxis: {
                    showgrid: false,
                    fixedrange: true,
                    visible: false,
                    // tickmode: 'array',
                    // tickvals: cumulDays,
                    // ticktext: seasonal_strings,
                    // ticks: ""
                },
            },
            width: `${width}`,
            height: `${height}`,
            showlegend: false,
            margin: {
                b: 0,
                l: 0,
                r: 0,
                t: 0,
            },
            paper_bgcolor: 'rgba(0, 0, 0, 0)',
            dragmode: false,
            annotations: annotations,
        }
    }

    return (
        <Plot
            data={data}
            layout={layout}
            config={{
                displayModeBar: false,
            }}
        />
    )
}