import React, { useState, useEffect } from 'react'

import { ScrollArea } from '@mantine/core';
import MyCarousel from './MyCarousel.tsx';
import { Container } from '@mantine/core';

import { useSelector, useDispatch } from 'react-redux'

const Outputs = () => {
  const displayGraphs = useSelector(state => state.graph.graphsVisible)

  const [windowSize, setWindowSize] = useState({
    width: window.innerWidth,
    height: window.innerHeight,
  });

  useEffect(() => {
    const handleResize = () => {
      setWindowSize({
        width: window.innerWidth,
        height: window.innerHeight,
      });
    };

    window.addEventListener('resize', handleResize);

    // Clean up the event listener on component unmount
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  const myStyle = {
    position: 'fixed',
    top: '0px',
    right: '0px',
    left: '0px',
    // height: '0px',
    // display: 'flex',
    // color: 'red',
    backgroundColor: 'blue',
    // width: '100%',
  }

  return (
    <div
    style={myStyle}
    className={displayGraphs ? 'graphContainerVisible' : 'graphContainerHidden'}
    >
      <MyCarousel
        windowSize={windowSize}  // Just here to force a rerender when window size changes, not actually used
      />
    </div>
  )
}

export default Outputs