import React, { ReactNode, useState } from 'react';
import { Combobox, Group, Input, InputBase, ActionIcon, useCombobox, Center } from '@mantine/core';
import cx from 'clsx';
import classes from './DropdownOptionsAnimation.module.css';
import CloudIcon from './icons/CloudIcon.jsx';
import FactoryIcon from './icons/FactoryIcon.jsx';
import ThermometerIcon from './icons/ThermometerIcon.jsx';
import TornadoIcon from './icons/TornadoIcon.jsx';
import SeaIcon from './icons/SeaIcon.jsx';

interface Item {
  value: string;
  icon: ReactNode;
}

interface SelectOptionProps extends Item {
  inDropdown: boolean;
  buttonWidth: number;
}

const variableOptions: Item[] = [
  { value: 'Thermometer', icon: <ThermometerIcon /> },
  { value: 'Sea', icon: <SeaIcon /> },
  { value: 'Cloud', icon: <CloudIcon /> },
  { value: 'Tornado', icon: <TornadoIcon /> },
  { value: 'Factory', icon: <FactoryIcon /> },
];

function getItemForVal(value) {
  const index = variableOptions.findIndex(item => item.value === value);
  return variableOptions[index]
}

function SelectOption({ value, icon, inDropdown, buttonWidth }: SelectOptionProps) {
  const buttonStyle = {
    width: `${buttonWidth}px`,
    height: `${buttonWidth}px`,
    // padding: "0px 13px 0px 13px",
    padding: "15%",
    // margin: "0px",
    '--ai-bg': 'rgb(0, 0, 0, 0.2)',
    '--ai-hover': 'rgb(255, 255, 255, 0.3)',
    borderRadius: '100px',
    pointerEvents: "auto",
    borderColor: "#94133377",
    borderWidth: "2px",
  }
  
  const buttonStyleDropdown = {
    width: `${buttonWidth}px`,
    height: `${buttonWidth}px`,
    // padding: "0px 10px 0px 10px",
    padding: "15%",
    // margin: "0px",
    '--ai-bg': 'transparent',
    '--ai-hover': 'rgb(0, 0, 0, 0.3)',
    borderRadius: '100px',
    pointerEvents: "auto",
  }

  const myStyle = inDropdown ? buttonStyleDropdown : buttonStyle;  

  return (
    <Center>
      <ActionIcon
        component="div"
        variant="filled"
        style={myStyle}
      >
        {icon}
      </ActionIcon>
    </Center>
  );
}

export default function VariablesSelector({buttonWidth}) {


  const [animating, setAnimating] = useState(false);

  const combobox = useCombobox({
    onDropdownClose: () => {
      combobox.resetSelectedOption();
      setAnimating(false);
    },

    onDropdownOpen: () => setAnimating(true),
  });

  const [value, setValue] = useState<Item | null>(variableOptions[0]);
  const selectedOption = variableOptions.find((item) => item.value === value?.value);

  const options = variableOptions.map((item, index) => (
    <Combobox.Option
      value={item.value}
      key={item.value}
      className={cx({ [classes.animateOption]: animating })}
      onMouseOver={() => combobox.selectOption(index)}
      style={{
        animationDelay: `${index * 50}ms`,
        width: buttonWidth,
        color: 'transparent',
        backgroundColor: 'transparent',
        padding: 0,
      }}
    >
      <SelectOption {...item} inDropdown={true} buttonWidth={buttonWidth} />
    </Combobox.Option>
  ));

  return (
    <Combobox
      store={combobox}
      withinPortal={false}
      // width={987}
      offset={-2 - buttonWidth}
      onOptionSubmit={(val) => {
        const itemForVal = getItemForVal(val)
        setValue(itemForVal);
        combobox.closeDropdown();
      }}
    >
      <Combobox.Target>
        <InputBase
          component="button"
          type="button"
          pointer
          onClick={() => combobox.toggleDropdown()}
          rightSectionPointerEvents="none"
          multiline
          // style={{
          //   padding: 0,
          //   borderRadius: 100
          // }}
          styles={{
            root: {
              // padding: '0',
              // color: 'black',
              // backgroundColor: 'black',
              // '--ai-bg': 'rgb(0, 0, 0, 0.2)',
              // '--ai-hover': 'rgb(255, 255, 255, 0.3)',
            },
            input: {
              padding: '0',
              borderRadius: '100px',
              // color: 'black',
              backgroundColor: 'transparent',
              borderColor: 'transparent',
              // opacity: 0.5,
              // '--ai-bg': 'rgb(0, 0, 0, 0.2)',
              // '--ai-hover': 'rgb(255, 255, 255, 0.3)',
            },
          }}
        >


          {selectedOption ? (
            <SelectOption {...selectedOption} inDropdown={false} buttonWidth={buttonWidth} />
          ) : (
            <Input.Placeholder>

            </Input.Placeholder>
          )}
        </InputBase>
      </Combobox.Target>

      <Combobox.Dropdown
        style={{
          padding: 0,
          borderRadius: 100,
          gap: 0,
          width: buttonWidth + 4,
          margin: 0,
          backgroundColor: 'white',
          borderColor: "#94133377",
          borderWidth: 2,
        }}
        onMouseLeave={() => combobox.resetSelectedOption()}
      >
        <Combobox.Options>
          {options}
        </Combobox.Options>
      </Combobox.Dropdown>
    </Combobox>
  );
}

