import React, { useState } from 'react'
import { Carousel, CarouselProps } from '@mantine/carousel';
import MyCard from './MyCard.tsx';
import ScienceBox from './ScienceBox.tsx';
import '@mantine/carousel/styles.css';
import { useMediaQuery } from '@mantine/hooks';
import { useMantineTheme, Container, Paper, Indicator } from '@mantine/core';
import classesHoriz from './CarouselHoriz.module.css';
import classesVert from './CarouselVert.module.css';
import { useSelector } from 'react-redux';

export default function MyCarousel({ windowSize }) {
  const theme = useMantineTheme();
  const mobile = useMediaQuery(`(max-width: ${theme.breakpoints.sm})`);

  const position = useSelector(state => state.location.locationPosition)

  const slideAspectRatio = 1.7  // Height / width of the card
  let landscape = true
  if (window.innerWidth < window.innerHeight) {
    landscape = false
  }

  // let height = 400
  let slideWidth = window.innerWidth * 0.85
  let slideHeight = slideWidth * slideAspectRatio
  let height = slideHeight

  let orientation: CarouselProps["orientation"] = 'horizontal'

  let myStyle, slideGap, classNames


  if (landscape) {
    // If landscape then put scroll on the right
    height = window.innerHeight
    orientation = 'vertical'

    slideHeight = Math.min(window.innerHeight * 0.9, 1000)
    const slideWidthFromHeight = slideHeight / slideAspectRatio
    const maxSlideWidthRatio = 0.32
    const slideWidthFromWidth = window.innerWidth * maxSlideWidthRatio
    slideWidth = Math.min(slideWidthFromHeight, slideWidthFromWidth)
    slideGap = slideHeight * 0.02

    // const carouselWidth = slideWidth
    myStyle = {
      position: 'fixed',
      right: "0px",
      // width: {carouselWidth},
      // minWidth: {carouselWidth},
      // maxWidth: {carouselWidth},
      height: { height },
      minHeight: { height },
      maxHeight: { height },
      top: '0px',
      padding: '0px 10px 0px 10px',
      backgroundColor: 'rgba(0, 0, 0, 0.3)',
    }
  } else {
    // If portrait (i.e. mobile) then put scroll on the bottom and ensure that the overall height of the slide won't be too large
    const maxSlideHeightRatio = 0.66
    const maxHeight = window.innerHeight * maxSlideHeightRatio
    const slideHeightRatio = slideHeight / maxHeight
    // console.log('slideHeightRatio', slideHeightRatio)
    if (slideHeightRatio > 1) {
      // slideWidth /= slideHeightRatio
      slideHeight /= slideHeightRatio
    }
    const minAspectRatio = 1.2
    // console.log('minAspectRatio', minAspectRatio, slideHeight / slideWidth)
    if ((slideHeight / slideWidth) < minAspectRatio) {
      slideWidth  = slideHeight / minAspectRatio
    }
    slideGap = slideWidth * 0.05
    orientation = 'horizontal'
    height = slideHeight
    myStyle = {
      position: 'fixed',
      // right: "0px",
      width: '100%',
      minWidth: '100%',
      maxWidth: '100%',
      bottom: `-${window.innerHeight}px`,
      justifyContent: "center",
      padding: '10px 0px 10px 0px',
      backgroundColor: 'rgba(0, 0, 0, 0.3)',
    }
    // classNames=classHoriz
  }


  return (
    <div style={myStyle}>
      <Carousel
        withIndicators
        height={height}
        orientation={orientation}
        slideSize={{ base: `${slideWidth}` }}
        slideGap={slideGap}
        loop
        slidesToScroll={1}
        align='center'
        classNames={landscape ? classesVert : classesHoriz}
        styles={{
          root: {
            // touchAction: "none",
          },
          indicator: {
            backgroundColor: "#941333",
          }
        }}
      >
        <Carousel.Slide>
          <ScienceBox width={slideWidth} height={slideHeight} position={position} variable={"Rainfall annual climatology"} landscape={landscape} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox width={slideWidth} height={slideHeight} position={position} variable={"Temperature annual climatology"} landscape={landscape} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox width={slideWidth} height={slideHeight} position={position} variable={"Rainfall annual timeseries"} landscape={landscape} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox width={slideWidth} height={slideHeight} position={position} variable={"Temperature annual timeseries"} landscape={landscape} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox width={slideWidth} height={slideHeight} position={position} variable={"Temperature warming stripes"} landscape={landscape} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox width={slideWidth} height={slideHeight} position={position} variable={"Wind-speed annual climatology"} landscape={landscape} />
        </Carousel.Slide>
        <Carousel.Slide>
          <ScienceBox width={slideWidth} height={slideHeight} position={position} variable={"Temperature daily climatology"} landscape={landscape} />
        </Carousel.Slide>

      </Carousel>
    </div>
  );
}

// It would be good to hide the left/right buttons on mobile screens