import React, { useState, useEffect, useRef } from 'react'
import { MapContainer, TileLayer, WMSTileLayer, AttributionControl, Popup, ZoomControl, useMapEvents, Marker, useMap, GeoJSON } from 'react-leaflet';
import ArcticCRS from './Projections.jsx';
import Leaflet, { Icon } from 'leaflet'
// import { isMobile } from 'react-device-detect';
import { Title } from '@mantine/core';
import { labelFromAdminLevels } from './Location.ts'

// import Geolocation from './Geolocation.tsx';

import { useSelector, useDispatch } from 'react-redux'
import { updatePosition, updateName } from '../reducers/locationSlice.js'
// let defaultPosition = [51, 0]

// const arcticTiles = "https://tile.gbif.org/3575/omt/{z}/{x}/{y}@4x.png?style=osm-bright-en"
const mercatorTiles = "https://{s}.tile.osm.org/{z}/{x}/{y}.png"

const mapStyle = {
  position: 'fixed',
  left: '0px',
  right: '0px',
  top: '0px',
  bottom: '0px',
}

const c3sred = '#941333'

const Viewer = () => {
  const initialPosition = useSelector(state => state.location.locationPosition)
  const initialPlaceName = useSelector(state => state.location.locationName)
  const haveGeolocatedFromState = useSelector(state => state.location.haveGeolocated)
  const graphsVisible = useSelector(state => state.graph.graphsVisible)
  const dispatch = useDispatch()

  const [haveGeolocated, setHaveGeolocated] = useState(haveGeolocatedFromState);

  const [position, setPosition] = useState(initialPosition)
  const [placeName, setPlaceName] = useState(initialPlaceName)

  useEffect(() => {
    setPosition(initialPosition)
    setPlaceName(initialPlaceName)
    // For some reason, I don't want to make this also depend on initialPlaceName, as it means the map won't let you move
  }, [initialPosition])

  const PanToLocation = ({ position }) => {
    const map = useMap();

    const zoom = map.getZoom()

    // Zoom 5, offset 9
    // Zoom 2.5, offset 50
    // Zoom 7, offset 2
    // Zoom 4, offset 20

    // Zoom 6.5, offset 3
    // Zoom 6, offset 4
    // Zoom 5.5, offset 6
    // Zoom 4.5, offset 13
    // Zoom 3.5, offset 27
    // Zoom 3, offset 37

    useEffect(() => {
      const controller = new AbortController()
      
      if (position) {
        let landscape = true
        if (window.innerWidth < window.innerHeight) {
          landscape = false
        }

        // TODO: DO similar offset for longitude for desktop?
        
        const degToRad = Math.PI / 180
        const latFactor = Math.cos(position.lat * degToRad)
        const sizeFactor = window.innerHeight / 700
        const offset = (294.67 * Math.exp(-0.69 * zoom) - 0.48) * latFactor * sizeFactor
        const latOffset = ((!landscape && graphsVisible) ? offset : 0)
        const positionMod = { lat: position.lat - latOffset, lng: position.lng };
        map.panTo(positionMod, {
          animate: true,        // Whether to animate the transition
          duration: 0.5,        // Duration of the transition in seconds
          easeLinearity: 0,   // How linear the easing function is (0 - 1)
        });
        // flyToWithPadding(map, position);
      }

      return () => controller.abort()
    }, [position, map]);

    return null;
  };

  function flyToWithPadding(map, latlng, options = {}) {
    const bounds = map.getBounds();
    const padding = 10; // Adjust this value as needed
    const paddedBounds = bounds.pad(padding);
  
    if (!paddedBounds.contains(latlng)) {
      const newLatLng = {
        lat: Math.min(Math.max(latlng.lat, paddedBounds.getSouth()), paddedBounds.getNorth()),
        lng: latlng.lng,
      };
      map.flyTo(newLatLng, options.zoom, options);
    } else {
      map.flyTo(latlng, options.zoom, options);
    }
  }

  const corner1 = Leaflet.latLng(-79, -Infinity)
  const corner2 = Leaflet.latLng(82, Infinity)
  const bounds = Leaflet.latLngBounds(corner1, corner2)

  function normalizeLongitude(longitude) {
    // Normalize the longitude to be within the range of -180 to 180
    longitude = ((longitude + 180) % 360 + 360) % 360 - 180;
    return longitude;
  }

  const findNearestPlace = async (latitude, longitude) => {
    const longitudeWrapped = normalizeLongitude(longitude)

    const url = `https://nominatim.openstreetmap.org/reverse?format=geocodejson&lat=${latitude}&lon=${longitudeWrapped}&addressdetails=1`;
    console.log(`Used URL: ${url}`);
    try {
      const response = await fetch(url);
      const data = await response.json();

      const adminLevels = data.features[0].properties.geocoding.admin;
      const city = labelFromAdminLevels(adminLevels)

      // setPlace(city);   
      dispatch(updateName(city))
      setPlaceName(city)
      console.log(`Got city: ${city}`);
    } catch (error) {
      dispatch(updateName(''))
      setPlaceName('')
      console.log("Error fetching data from Nominatim.");
    }
  };

  // Hook to handle map events
  const MapEventsComponent = () => {
    const abortController = new AbortController()

    useMapEvents({
      click(e) {
        abortController.abort()
        const { lat, lng } = e.latlng;
        // dispatch(setGeolocated()); // Don't try and geolocate if we've alreadty clicked away
        // setHaveGeolocated(true)
        setHaveGeolocated(true)
        setPosition({ lat, lng });
        dispatch(updatePosition({ lat: lat, lng: lng }));

        // You can use lat and lng elsewhere here
        console.log(`Clicked at latitude: ${lat}, longitude: ${lng}`);
        findNearestPlace(lat, lng);
      },
    });
    return null;
  };
  
  const bbox = [-90, -180, 90, 180]; // Example bounding box for the entire world

  const fetchCapitalCities = async (bbox) => {
    const overpassUrl = 'https://overpass-api.de/api/interpreter';
    const query = `
      [out:json];
      (
        node[capital=yes](${bbox.join(',')});
        node[capital=2](${bbox.join(',')});
      );
      out body;
    `;
    console.log(`Using Overpass API query: ${query}`);
    const response = await fetch(overpassUrl, {
      method: 'POST',
      body: query
    });
    const data = await response.json();
    return data.elements.map(element => ({
      type: 'Feature',
      geometry: {
        type: 'Point',
        coordinates: [element.lon, element.lat]
      },
      properties: {
        name: element.tags.name,
        type: element.tags.place
      }
    }));
  };

  const fetchLargeSettlements = async (bbox) => {
    const minPop = 1000000;
    const overpassUrl = 'https://overpass-api.de/api/interpreter';
    const query = `
      [out:json];
      (
        node["place"="city"](${bbox.join(',')});
        way[place=city](${bbox.join(',')});
        relation[place=city](${bbox.join(',')});
      );
      out body;
      >;
      out skel qt;
    `;
    console.log(overpassUrl, query);
    const response = await fetch(overpassUrl, {
      method: 'POST',
      body: query
    });
    const data = await response.json();
    console.log('RECEIVED', data);

    // Filter elements based on population
    const filteredElements = data.elements.filter(element => {
      const population = element.tags && parseInt(element.tags.population, 10);
      return population && population >= minPop;
    });
    console.log('FILTERED', filteredElements)

    // Map filtered elements to GeoJSON format
    return filteredElements.map(element => ({
      type: 'Feature',
      geometry: {
        type: element.type === 'node' ? 'Point' : 'Polygon',
        coordinates: element.type === 'node'
          ? [element.lon, element.lat]
          : element.geometry.map(coord => [coord.lon, coord.lat])
      },
      properties: {
        name: element.tags.name,
        type: element.tags.place,
        population: element.tags.population
      }
    }));
  };

  const fetchCapitalsFromFile = async () => {
    const response = await fetch('./capitals.json');
    const data = await response.json();
    return data.elements.map(element => ({
      type: 'Feature',
      geometry: {
        type: 'Point',
        coordinates: [element.lon, element.lat]
      },
      properties: {
        name: element.tags.name,
        type: element.tags.place
      }
    }));
  };

  const CapitalCitiesLayer = ({ bbox }) => {
    const [geojson, setGeojson] = useState(null);

    useEffect(() => {
      const fetchData = async () => {
        console.log('GETTING CAPITALS!')
        const data = await fetchLargeSettlements(bbox);
        // const data = await fetchCapitalCities(bbox);
        // const data = await fetchCapitalsFromFile();
        setGeojson({ type: 'FeatureCollection', features: data });
      };

      fetchData();
    }, [bbox]);

    return geojson ? <GeoJSON data={geojson} /> : null;
  };

  // Adjust zoom levels dynamically
  const viewportWidth = window.innerWidth;
  const viewportHeight = window.innerHeight;
  const maxViewportDimension = Math.max(viewportWidth, viewportHeight);

  let minZoom, maxZoom;
  let zoomSnap = 1
  const offset = 1
  if (maxViewportDimension < 700) {
    minZoom = 2
    maxZoom = 7 - offset
    zoomSnap = 0.5
  } else if (maxViewportDimension < 1000) {
    minZoom = 2.5
    maxZoom = 8 - offset
    zoomSnap = 0.5
  } else if (maxViewportDimension < 1200) {
    minZoom = 3
    maxZoom = 8 - offset
    zoomSnap = 1
  // } else if (maxViewportDimension < 1400) {
  //   minZoom = 3
  //   maxZoom = 8 - offset
  //   zoomSnap = 1
  } else if (maxViewportDimension < 1500) {
    minZoom = 3
    maxZoom = 9 - offset
    zoomSnap = 1
  } else if (maxViewportDimension < 2000) {
    minZoom = 3
    maxZoom = 9 - offset
    zoomSnap = 1
  } else {
    minZoom = 3
    maxZoom = 9 - offset
  }
  
  const [landscape, setLandscape] = useState(window.innerHeight < window.innerWidth)

  // const [windowSize, setWindowSize] = useState({
  //   width: window.innerWidth,
  //   height: window.innerHeight,
  // });

  useEffect(() => {
    const handleResize = () => {
      setLandscape(window.innerHeight < window.innerWidth);
    };

    window.addEventListener('resize', handleResize);

    // Clean up the event listener on component unmount
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, []);


  return (
    <>
      {/* <Geolocation /> */}

      <MapContainer
        style={mapStyle}
        attributionControl={false}
        zoomControl={false}
        zoomSnap={zoomSnap}
        center={position}
        zoom={minZoom + 1}
        bounceAtZoomLimits={true}



        maxBounds={bounds}
        maxBoundsViscosity={0}
        minZoom={minZoom}
        maxZoom={maxZoom}
      // whenCreated={({ target }) => setMap(target)}
      >

        <TileLayer
        attribution="Powered by the <a href='https://cds-beta.climate.copernicus.eu/' target='_blank'><span style='color:#941333;'>Copernicus Climate Data Store</span></a>"
        url={mercatorTiles}
      />
        <WMSTileLayer
          url="https://cds.climate.copernicus.eu/cache-compute-0001/cache/publicapps2/livemap.plot-1665219098.2719853-9719-12-0d910088-9904-4ab5-8476-173b81706aaa.nc.wms?"
          layers='t2m'
          format='image/png'
          pane="tilePane"
          opacity={0.4}
        />
        <WMSTileLayer
          url='https://cds.climate.copernicus.eu/geoserver/wms?'
          layers='basic:coastlines__resolution_high'
          transparent={true}
          format='image/png'
          pane="shadowPane"
        />
        <WMSTileLayer
          url='https://cds.climate.copernicus.eu/geoserver/wms?'
          layers='basic:country_units__resolution_high'
          transparent={true}
          format='image/png'
          pane="shadowPane"
        />
        <WMSTileLayer
          url='https://cds.climate.copernicus.eu/geoserver/wms?'
          layers='basic:states_provinces__resolution_high'
          transparent={true}
          format='image/png'
          pane="shadowPane"
          opacity={0.2}
        />
        <WMSTileLayer
          url='https://cds.climate.copernicus.eu/geoserver/wms?'
          layers='basic:lakes__resolution_high'
          transparent={true}
          format='image/png'
          pane="shadowPane"
          opacity={0.2}
        />
        <WMSTileLayer
          url='https://cds.climate.copernicus.eu/geoserver/wms?'
          layers='basic:rivers__resolution_high'
          transparent={true}
          format='image/png'
          pane="shadowPane"
          opacity={0.2}
        />
        {/* <WMSTileLayer
        url='https://cds.climate.copernicus.eu/geoserver/wms?'
        layers='basic:cities__resolution_high'
        transparent={true}
        format='image/png'
        pane="shadowPane"
        opacity={1}
      /> */}

        <AttributionControl position="bottomright" prefix={false} />
        {landscape && <ZoomControl position="bottomleft" />}
        <MapEventsComponent />

        <Marker position={position} color={'red'}>
          <Popup>
            <Title order={4}>{placeName}</Title>
            Latitude: {position.lat.toFixed(2)}<br />Longitude: {position.lng.toFixed(2)}
          </Popup>
        </Marker>

        <PanToLocation position={position} />
        {/* <CapitalCitiesLayer bbox={bbox} /> */}
      </MapContainer>
    </>
  )
}

export default Viewer
