import React, { useState, useRef } from 'react';
import { DatePicker } from '@mantine/dates';
import { useClickOutside } from '@mantine/hooks';
import { useSelector, useDispatch } from 'react-redux'
import { hideCalendar } from '../reducers/calendarSlice.js'
import { Container } from '@mantine/core';

const datePickerStyle = {
  maxWidth: "300px",
  backgroundColor: "white",
  borderRadius: 10,
}

function MyDatePicker() {
  const [value, setValue] = useState<Date | null>(null);

  const displayCalendar = useSelector(state => state.calendar.calendarVisible)
  const dispatch = useDispatch()

  // const handleClickOutside = () => {
  //   dispatch(hideCalendar());
  // };

  // const ref = useClickOutside(handleClickOutside, ['mousedown']);
  
  return (
    <>
      <DatePicker 
        value={value}
        onChange={setValue}
        style={datePickerStyle}
        className={displayCalendar ? 'graphContainerVisible' : 'graphContainerHidden'}
        // ref={ref}
      />
    </>
  );
}

export default MyDatePicker;