import React, { useState, useEffect } from 'react'

import { Stack, Grid, Group, Space } from '@mantine/core';

import '@mantine/core/styles.css';

// import Icon from './Icon';
// import {DropdownOptionsAnimation} from './DropdownOptionsAnimation.tsx';
import VariablesSelector from './VariablesSelector.tsx';
// import LocationSearchBar from './LocationSearchBarAuto.tsx';
import LocationSearchBar from './LocationSearchBarFree.tsx';
import MyDatePicker from './MyDatePicker.tsx';
import ToggleGraphs from './ToggleGraphs.tsx';
import ToggleCalendar from './ToggleCalendar.tsx';
import { Container } from 'semantic-ui-react';

function calculateButtonBarWidths(buttonGap, margin, midGap) {
    const buttonWidthFraction = 0.4
    const totalWidth = window.innerWidth - margin * 2;
    const availableButtonWidth = totalWidth * buttonWidthFraction - 2 * buttonGap - midGap / 2
    const buttonWidth = availableButtonWidth / 3
    const barWidth = totalWidth * (1 - buttonWidthFraction) - 0.5 * midGap
    return [buttonWidth, barWidth]
}

// TODO: Now that I have sorted the width, actually apply the styling correctly
// Note that buttons are covered on desktop...!
// Align calendar on RHS now (using same margin)
// Make this useEffect to change


const Inputs = () => {
    const maxButtonWidth = 55;
    const maxBarWidth = 300;
    const gap = 10
    const margin = 15
    const midGap = 25

    let widths
    const narrowScreen = Math.min(window.innerWidth, window.innerHeight) < 550;
    if (!narrowScreen) {
        widths = [maxButtonWidth, maxBarWidth]
    } else {
        widths = calculateButtonBarWidths(gap, margin, midGap);
    }

    // TODO: Use useEffect to update buttonWidth when window size changes
    const buttonWidth = widths[0];
    const barWidth = widths[1];

    const mysteryExtra = 2
    const totalWidth = barWidth + buttonWidth * 3 + 2 * gap + midGap + margin;
    const remainingWidth = window.innerWidth - totalWidth
    // const remainingWidth = margin

    const leftColStyle = {
        position: 'fixed',
        top: `${margin}px`,
        left: `${margin}px`,
        pointerEvents: "none",
    }

    const leftColStyle2 = {
        position: 'fixed',
        top: `${margin}px`,
        left: `${barWidth + margin + midGap - mysteryExtra}px`,
        pointerEvents: "none",
    }

    // const rightColStyle = {
    //     position: 'fixed',
    //     top: `${margin}px`,
    //     right: `${margin}px`,
    //     pointerEvents: "none",
    // }

    const datePickerContainerStyle = {
        position: 'fixed',
        top: `${margin + buttonWidth + gap}px`,
        right: `${remainingWidth}px`,
        pointerEvents: "none",
    }

    const [windowSize, setWindowSize] = useState({
        width: window.innerWidth,
        height: window.innerHeight,
      });
    
      useEffect(() => {
        const handleResize = () => {
          setWindowSize({
            width: window.innerWidth,
            height: window.innerHeight,
          });
        };
    
        window.addEventListener('resize', handleResize);
    
        // Clean up the event listener on component unmount
        return () => {
          window.removeEventListener('resize', handleResize);
        };
      }, []);

    return (
        <>
            <Container
                gap={gap}
                justify="flex-start"
                align="flex-start"
                direction="column"
                wrap="wrap"
                style={leftColStyle}
                windowSize={windowSize}  // Just here to force a rerender when window size changes, not actually used
            >
                <LocationSearchBar barWidth={barWidth} />
            </Container>

            <Group
                gap={gap}
                justify="flex-end"
                align="flex-start"
                direction="column"
                wrap="wrap"
                // style={narrowScreen ? leftColStyle2 : rightColStyle}
                style={leftColStyle2}
                windowSize={windowSize}  // Just here to force a rerender when window size changes, not actually used
            >
                <VariablesSelector buttonWidth={buttonWidth} />
                <ToggleCalendar buttonWidth={buttonWidth} />
                <ToggleGraphs buttonWidth={buttonWidth} />
            </Group>

            <Container style={datePickerContainerStyle}>
                <MyDatePicker />
            </Container>
        </>
    );
}

export default Inputs